<template>
<div class="accordion" @click="open = !open" :class="{red__bg : open}">
  <div class="accordion__item">
    <div class="left__side" >
      <fa-icon icon="clock" :class="{rotate : open}"/>
      <p>{{ title }}</p>
    </div>
    <fa-icon icon="angle-down" :class="{rotate : open}" />
  </div>
    <p v-if="open">{{ description }}</p>
</div>
</template>

<script>
export default {
  name: "Accordion",
  props: {
    title: {
      type: String,
      default: 'Best price on the market'
    },
    description: {
      type: String,
      default: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aliquid amet in incidunt non optio quisquam.'
    }
  },
  data() {
    return {
      open: false
    }
  },
}
</script>

<style lang="scss">
.accordion {
  display: flex;
  justify-content: space-between;
  text-align: center;
  flex-direction: column;
  cursor: pointer;
  border-radius: 8px;
  padding: 0 15px;

  .accordion__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .left__side {
      display: flex;
      gap: 10px;
      align-items: center;
    }
  }
  p {
    text-align: left;
  }
}
.red__bg{
  background-color: #FB2D6A;
  color: #fff;
}
.rotate{
  color: #fff;
  transform: rotateX(180deg);
}

</style>